<the-card class="main" :frame="['head','body']"><template v-slot:head-left><div class="head-left font-size-16 font-weight-500 font-color-333333">记录转化</div></template><template v-slot:head-right><div class="head-right"><a-config-provider :auto-insert-space-in-button="false"><a-button class="hr-btn btns-radius-04 default-btns-blue" v-for="(v,k) in ['本日','本周','本月']" @click="typeSearch(k)" :type="btnType===k?'primary':'default'" :class="{'btn-color':btnType===k}">{{ ['本日','本周','本月'][k] }}</a-button></a-config-provider></div></template><template v-slot:body><a-spin :spinning="allLoading.transform"><div class="body"><div class="body-contain"><the-color-progress class="body-item" v-for="(v, k) in transformData" :key="`progress-${k+1}`" :title="v.title" :number="v.number" :percent="v.percent" :pgColor="v.pgColor" :bgColor="v.bgColor"></the-color-progress></div></div></a-spin></template></the-card>